<template>
	<div class="dropdown" :class="{'is-active' : active }">
		<slot></slot>
	</div>
</template>

<script>
export default {

  name: 'dropdown',

  props: ['show'],
  data () {
    return {
    	active: false
    }
  },
  methods:{
  	inactivate(){
  		this.active = false
  		this.$emit('update:show',false)
  	}
  },
  watch: {
  	show(){
  		this.active = this.show
  	}
  }
}
</script>

<style lang="css" scoped>
</style>


