<template>
	<div class="block">
		<p class="title" :class="modifiers">{{this.heading}}</p>
		<hr>

		<div class="content">
			<slot></slot>
		</div>
	</div>
</template>

<script>
export default {

  name: 'custom-section',
  props: ['heading','modifiers'],
  data () {
    return {

    }
  }
}
</script>

<style lang="css" scoped>
	.block{
		background: #fff;
		padding: 20px;
	}

  .block:last-child{
    margin-bottom: 5px;
  }
</style>


