<template>
	<nav class="navbar is-fixed-top is-primary" role="navigation" aria-label="main navigation">
		<div class="navbar-brand">
			<span class="navbar-item">Biodata</span>
			<div class="navbar-burger" :class="{ 'is-active': isActiveMenu }" @click.stop="toggleMenu">
				<span></span>
				<span></span>
				<span></span>
			</div>
		</div>
		<!-- ./navbar brand -->

		<div class="navbar-menu" :class="{ 'is-active' : isActiveMenu }">
			<div class="navbar-start">
				<a href="" class="navbar-item">Biodata</a>
				<a href="" class="navbar-item">Resume</a>
				<a href="" class="navbar-item">Curriculam vitae</a>
				<a href="" class="navbar-item">Portfolio</a>
				<a href="" class="navbar-item">Others</a>
			</div>

			<div class="navbar-end">
				<div class="navbar-item">
					<div class="field">
						<p class="control has-icons-right">
							<input type="text" class="input is-rounded is-primary" placeholder="Search">
							<span class="icon is-small is-right">
								<i class="fa fa-search"></i>
							</span>
						</p>
					</div>	
				</div>
				
			</div>
		</div>


		<!-- ./navbar-menu -->
	</nav>
</template>

<script>
export default {

  name: 'navbar',

  data () {
    return {
    	isActiveMenu: false
    }
  },
  methods:{
  	toggleMenu(){
  		this.isActiveMenu = !this.isActiveMenu
  	}
  }
}
</script>

<style lang="css" scoped>
</style>


